import React from "react";
import Draggable from "react-draggable";

export default function MsItem({ image, onRightClick, onUpdatePos }) {
  const handleClick = (e) => {
    e.preventDefault();
    onRightClick();
  };

  const handleDrag = (event, data) => {
    onUpdatePos({ x: data.x, y: data.y });
  };

  return (
    <Draggable
      defaultPosition={{ x: 0, y: 0 }}
      bounds={{ left: 0, right: 295, top: 0, bottom: 185 }}
      onStop={handleDrag}
    >
      <img
        src={image}
        height={65}
        draggable={false}
        width={65}
        style={{
          cursor: "pointer",
          display: "inline",
          position: "absolute",
        }}
        onContextMenu={handleClick}
      />
    </Draggable>
  );
}
