import theme from "./theme";
import MsPicker from "./components/MsPicker/MsPicker";
import MsTitle from "./components/MsTitle/MsTitle";
import { useState } from "react";
import MsGrid from "./components/MsGrid/MsGrid";
import toast, { Toaster } from "react-hot-toast";
import MsButton from "./components/MsButton/MsButton";
import MsHelp from "./components/MsHelp/MsHelp";

function App() {
  const [placedItems, setPlacedItems] = useState([]);

  const addItem = (item) => {
    if (
      placedItems.length > 2 ||
      placedItems.find((i) => i.type === item.type)
    ) {
      toast.error(
        "Max 3 unique accessoires allowed! Remove one by right clicking it!"
      );
      return;
    }
    setPlacedItems((cur) => [...cur, item]);
  };

  const deleteItem = (item) => {
    setPlacedItems((cur) => cur.filter((i) => i.type !== item.type));
  };

  const updateItem = (item, pos) => {
    let instance = placedItems.find((i) => i.type === item.type);
    if (instance) {
      instance.pos = pos;
      setPlacedItems((cur) => [
        ...cur.filter((i) => i.type !== item.type),
        instance,
      ]);
    }
  };

  return (
    <>
      <Toaster />
      <div className="w-full p-4">
        <main
          role="main"
          className="w-full flex flex-col h-screen content-center justify-center"
        >
          <div className="w-full lg:w-1/2 bg-gray-50 rounded-xl m-auto">
            <div
              className="rounded shadow px-4 pt-5 pb-4 sm:p-6 sm:pb-4 flex flex-col items-center"
              style={{ backgroundColor: theme.grey }}
            >
              <MsTitle />
              <MsGrid
                placedItems={placedItems}
                deleteItem={deleteItem}
                updateItem={updateItem}
              />
              <MsPicker addItem={addItem} />
              <MsHelp />
              <div className="grid content-center">
                <MsButton placedItems={placedItems} />
              </div>
            </div>
          </div>
        </main>
      </div>
    </>
  );
}

export default App;
