import React from "react";
import itemtypes from "../../itemtypes";
import theme from "../../theme";

import MsPickerItem from "./MsPickerItem";

export default function MsPicker({ addItem }) {
  return (
    <div
      style={{
        alignItems: "center",
        backgroundColor: theme.grey2,
        borderRadius: 7.5,
        display: "flex",
        height: 75,
        justifyContent: "center",
        marginBottom: 15,
        overflow: "hidden",
      }}
    >
      {itemtypes.map((item) => (
        <MsPickerItem
          key={item.type}
          type={item.type}
          image={item.image}
          onClick={() => addItem(item)}
        />
      ))}
    </div>
  );
}
