import React, { useState } from "react";
import toast from "react-hot-toast";
import theme from "../../theme";
import { triggerBase64Download } from "react-base64-downloader";

export default function MsButton({ placedItems }) {
  const [isFetching, setIsFetching] = useState(false);

  const buttonClicked = async () => {
    if (!placedItems.length) {
      toast.error("Vanilla MsFrog rocks, but please add at least something...");
      return;
    }
    if (isFetching) return;

    setIsFetching(() => true);

    let jsonData = [];
    placedItems.forEach((item) => {
      jsonData.push({ type: item.type, pos: item.pos });
    });

    try {
      let response = await fetch("/api/generate", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(jsonData),
      });
      if (response.status !== 200)
        throw `Unexpected response: ${response.status}`;
      response = await response.json();
      if (!response.msfrog) throw "Empty response, the heck?";
      toast.success("Enjoy your MsFrog :D");
      triggerBase64Download(
        `data:image/png;base64,${response.msfrog}`,
        "msfrog"
      );
    } catch (e) {
      toast.error(`Sadge, something went wrong: ${e}`);
    }
    setIsFetching(() => false);
  };

  return (
    <>
      {isFetching ? (
        <button
          className="text-white font-bold py-2 px-4 rounded-full m-auto text-2xl"
          style={{ backgroundColor: theme.grey2 }}
        >
          Lemonthinking about it...
        </button>
      ) : (
        <button
          className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-full m-auto text-2xl"
          onClick={buttonClicked}
        >
          Generate
        </button>
      )}
    </>
  );
}
