import React from "react";
import msfrog from "../../assets/msfrog.png";
import theme from "../../theme";
import MsItem from "../MsItem/MsItem";

export default function MsGrid({ placedItems, deleteItem, updateItem }) {
  return (
    <div
      className="rounded-sm border-2 mb-2.5 center"
      style={{
        height: 250,
        backgroundColor: theme.white,
        backgroundImage: `url(${msfrog})`,
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center",
        position: "relative",
        width: 375,
      }}
    >
      {placedItems.map((item) => (
        <MsItem
          key={item.type}
          image={item.image}
          onRightClick={() => deleteItem(item)}
          onUpdatePos={(newPos) => updateItem(item, newPos)}
        />
      ))}
    </div>
  );
}
